<template>
  <div class="d-flex">
    <div class="col-12 table-wrapper">
      <table class="student-table table table-bordered table-hover table-sm border-secondary rounded-5">
        <thead>
          <tr class="table-primary text-center text-capitalize text-nowrap">
            <th class="fw-bolder" scope="col">Nomor</th>
            <th class="fw-bolder" scope="col">email</th>
            <th class="fw-bolder" scope="col">nama</th>
            <th class="fw-bolder" scope="col">kelas</th>
            <th class="fw-bolder" scope="col">no. Absen</th>
            <th class="fw-bolder" scope="col">status</th>
          </tr>
        </thead>
        <tbody v-if="AppStore().allStudent.isGettingData">
          <tr>
            <td class="text-center text-danger fw-bolder" scope="row" colspan="5">
              <div class="d-flex justify-content-center align-items-center">
                <div class="col-auto">Mengambil data siswa</div>
                <div class="col-auto">
                  <components-loader />
                </div>
              </div>
            </td>
          </tr>
        </tbody>
        <tbody v-if="!AppStore().allStudent.isGettingData &&
          AppStore().allStudent.data.length !== 0
          ">
          <tr class="text-lowercase text-nowrap" v-for="(student, index) in AppStore().allStudent.data" :key="index">
            <th class="text-center" scope="row">{{ Number(index) + 1 }}</th>
            <td>{{ Object(student).email }}</td>
            <td class="w-100">{{ Object(student).appData.name }}</td>
            <td class="w-100">{{ Object(student).appData.class }}</td>
            <td>{{ Object(student).appData.ruleNumber }}</td>
            <td>
              <div class="container">
                <div class="d-flex align-items-center gap-3">
                  <span
                    :class="['col-auto fw-bolder p-0', Object(student).status === 'nonactive' ? 'text-danger' : 'text-success']">
                    {{ Object(student).status }}
                  </span>
                  <button class="col-auto button-style small bg-success text-light" style="width: fit-content"
                    v-if="Object(student).status === 'nonactive'">
                    aktivasi sekarang
                  </button>
                  <button class="col-auto button-style small bg-danger text-light" style="width: fit-content" v-else>
                    hapus
                  </button>
                </div>
              </div>
            </td>
          </tr>
          <tr v-if="!AppStore().allStudent.isGettingData &&
            AppStore().allStudent.data.length === 0
            ">
            <td class="text-center text-danger fw-bolder" scope="row" colspan="6">
              <div class="container">
                <div class="d-flex">
                  <div class="col">
                    Tidak ada data siswa
                  </div>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script setup lang="ts">
if (AppStore().allStudent.lastGettingData === 0 && AppStore().appData.appRef) {
  AppDB.getAllStudentData();
}
</script>

<style scoped lang="scss">
.table-wrapper {
  overflow: auto;
}
</style>
